@import url('https://fonts.googleapis.com/css2?family=Foldit:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Foldit:wght@600&family=Konkhmer+Sleokchher&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Bruno+Ace+SC&display=swap');


*{
    margin: 0;
    padding: 0;
}

.main{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
}

.headername{
    color: whitesmoke;
    margin: 1em;
}

.divider{
    opacity: 30%;
    height: 0.05em;
    background-color: whitesmoke;
    margin: 0.5em;
}

.info_box{
    justify-content: center;
    align-items: center;
    align-content: center;
    width: 30%;
    min-width: 200px;
    background-color: whitesmoke;
    border-radius: 2em;
    backdrop-filter: blur(2px);
    background-color: rgba(0, 0, 0, 0.5);
    overflow:hidden;
    scroll-behavior: auto;
}

.serachbox{
    width: 80%;
    height: 2em;
    border-radius: 2em;
    text-align: center;
}


.glow-on-hover {
    width: 40%;
    padding: 1em;
    outline: none;
    font-family: 'Bruno Ace SC', cursive;
    font-weight: 700;
    font-size: 50%;
    /* overflow: hidden; */
    letter-spacing:0.1em;
    margin: 1em;
    color: #fff;
    background: #002bff;
    cursor: pointer;
    position: relative;
    z-index: 0;
    border: 0.1em solid rgb(0, 179, 234);
    border-radius: 10px;
}

.glow-on-hover:before {
    content: '';
    background: linear-gradient(45deg, #ff0000, #ff7300, #fffb00, #48ff00, #00ffd5, #002bff, #7a00ff, #ff00c8, #ff0000);
    position: absolute;
    top: -2px;
    left:-2px;
    background-size: 400%;
    z-index: -1;
    filter: blur(5px);
    width: calc(100% + 4px);
    height: calc(100% + 4px);
    animation: glowing 20s linear infinite;
    opacity: 0;
    transition: opacity .3s ease-in-out;
    border-radius: 10px;
}

.glow-on-hover:active {
    color: #931818
}

.glow-on-hover:active:after {
    background: transparent;
}

.glow-on-hover:hover:before {
    opacity: 1;
}

.glow-on-hover:after {
    z-index: -1;
    content: '';
    position: absolute;
    width: 100%;
    height: 100%;
    background: #111;
    left: 0;
    top: 0;
    border-radius: 10px;
}

@keyframes glowing {
    0% { background-position: 0 0; }
    50% { background-position: 400% 0; }
    100% { background-position: 0 0; }
}